.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
  font-family: 'Barrio', cursive;
}

.App-intro {
  height: 12px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: orange;
  -webkit-animation: blink 1s step-end infinite;
  animation: blink 1s step-end infinite;
}

  @-webkit-keyframes blink {
    67% { opacity: 0 }
  }

  @keyframes blink {
    67% { opacity: 0 }
  }


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.RowContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 120px;
  -webkit-box-shadow: 00px 0px 0px #888888;
          box-shadow: 00px 0px 0px #888888;
}

.Square {
  font-family: 'Roboto Mono', monospace;
  border-style: solid;
  width: 120px;
  margin: 0;
  padding: 5px;
  background: white;
}

@media (max-width: 600px) {
    .App-header {
      width: 560px;
    }
    .RowContainer {
        width: 600px; /* 22px difference */
        height: 120px;
    }
    .Square {
      width: 120px;
    }
}



